@media (min-width: 768px) {
    .card-item.full-width {
        min-height: 130px;
    }
    .custom-swiper {
        position: relative;
        padding-left: 32px;
        padding-right: 32px;
    }

    .swiper-button-next {
        &::after {
            content: unset;
        }
        &.custom-next {
            width: 26px;
            display: block;
            right: 0;
            transform: rotate(180deg);
        }
    }
    .swiper-button-prev {
        &::after {
            content: unset;
        }
        &.custom-prev {
            width: 26px;
            display: block;
            left: 0;
            right: auto;
        }
    }
}

@media (min-width: 991px) {
    .swiper-scrollbar-custom .swiper-scrollbar-drag .minion:before {
        height: 80px;
        width: 80px;
        margin-top: -45px;
        left: -20px;
    }

    .actions .actions-btn.actions-btn-menu {
        display: none;
    }

    .header-search {
        position: relative;
    }
}

@media (min-width: 1024px) {
    .custom-swiper {
        padding-left: 80px;
        padding-right: 80px;
    }
    .swiper-button-next {
        &.custom-next {
            width: 48px;
            right: 0;
        }
    }
    .swiper-button-prev {
        &.custom-prev {
            width: 48px;
            left: 0;
        }
    }
}

@media (max-width: 991px) {
    .product-list {
        margin: 0;
        .product-item {
            flex: 0 0 100%;
            margin: 0;
        }
    }

  
}
@media (max-width: 767px) {
    .custom-swiper {
        .custom-next,
        .custom-prev {
            display: none;
        }
    }
}